<template>
  <div class="container">
    <div @click="fnShowInfo" class="goods-info-info-wrap">
      <div @click.stop class="content">
        <img @click="fnShowInfo" alt class="close" :src="`${$cdnImageUrl}/common/control-close.png`" />
        <div class="content-wrap">
          <div class="head">
            <div class="head-box">
              <div class="left">
                <div class="img-box" @click="fnPreviewImage(productInfo.pic)">
                  <img alt :src="productInfo ? productInfo.pic : '' | imageToHttps" />
                </div>
              </div>
              <div class="right">
                <div class="jq">

                  <!-- <span class="price-range" v-if="priceRange">
                    <span>
                      <span><span class="yuan">￥</span>{{ minPrice |formatPrice | formatPriceInt }}</span>
                      <span class="float-number" v-if="(Number(minPrice)*100) % 100 !== 0">
                        .{{minPrice|formatPrice | formatPriceFloat}}
                        </span>
                    </span>
                    <div class="price-devider"></div>
                    <span>
                      <span>{{ maxPrice |formatPrice | formatPriceInt }}</span>
                      <span class="float-number" v-if="(Number(maxPrice)*100) % 100 !== 0">
                        .{{maxPrice|formatPrice | formatPriceFloat}}
                        </span>
                    </span>
                    </span> -->
                    <span class="price-range" v-if="priceInfo && priceInfo.minOriginPrice && priceInfo.maxOriginPrice && btnType === 9">
                      <span>
                        <span class="yuan">¥</span>
                        <span class="rmb">{{ priceInfo.minOriginPrice |formatPrice | formatPriceInt }}</span>
                        <span class="float-number">.{{priceInfo.minOriginPrice|formatPrice | formatPriceFloat}}</span>
                      </span>
                      <div class="price-devider"></div>
                      <span>
                        <span>{{ priceInfo.maxOriginPrice |formatPrice | formatPriceInt }}</span>
                        <span class="float-number">.{{priceInfo.maxOriginPrice|formatPrice | formatPriceFloat}}</span>
                      </span>
                    </span>
                    <span class="price-range" v-else-if="priceInfo && priceInfo.minPrice && priceInfo.maxPrice && btnType !== 9">
                      <span>
                        <span class="yuan">￥</span>
                        <span class="rmb yuan-int">{{ priceInfo.minPrice |formatPrice | formatPriceInt }}</span>
                        <span class="float-number">.{{priceInfo.minPrice|formatPrice | formatPriceFloat}}</span>
                      </span>
                      <div class="price-devider"></div>
                      <span>
                        <span>{{ priceInfo.maxPrice |formatPrice | formatPriceInt }}</span>
                        <span class="float-number">.{{priceInfo.maxPrice|formatPrice | formatPriceFloat}}</span>
                      </span>
                    </span>
                    <!-- <span v-else-if="productInfo.price || productInfo.price === 0"> -->
                    <span v-else-if="priceInfo && (priceInfo.actualPrice || priceInfo.actualPrice === 0)">
                      <span class="yuan">￥</span>
                      <span class="rmb yuan-int">{{ ((btnType===4 || btnType === 9) ? priceInfo.originPrice:priceInfo.actualPrice) |formatPrice | formatPriceInt}}</span>
                      <span class="dot-after">.{{ ((btnType===4 || btnType === 9) ? priceInfo.originPrice:priceInfo.actualPrice) |formatPrice | formatPriceFloat }}</span>

                    <!-- <span class="rmb">{{ priceArr[0] }}</span> -->
                    <!-- <span class="dot-after" v-if="priceArr.length > 1">.{{ priceArr[1] }}</span> -->
                    </span>
                  </div>
                <p class="price-quan" v-if="getGoodsUseCouponInfo && getGoodsUseCouponInfo.voucherLen && getGoodsUseCouponInfo.type=='sheng' && this.baseInfo.showCoupons">预计到手价{{getGoodsUseCouponInfo.price.toFixed(2)}}</p>
                  <div class="member-price" v-if="priceInfo && priceInfo.minPlusPrice && priceInfo.maxPlusPrice && isShowMemberPrice">
                    <span>¥{{priceInfo.minPlusPrice | formatPrice}}-{{priceInfo.maxPlusPrice | formatPrice}}</span>
                    <img src="//cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/plus.png"/>
                  </div>
                  <div class="member-price" v-else-if="priceInfo && priceInfo.plusPrice && isShowMemberPrice">
                    <span>¥{{priceInfo.plusPrice | formatPrice}}</span>
                    <img src="//cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/plus.png"/>
                  </div>
                <!-- 佣金 -->
                <div @click="$emit('showCommissionInfo')" class="commission-w" v-if="displayCommission && (priceInfo.commission || priceInfo.bounty)">
                  <commission-label :commission="priceInfo.maxCommission ? priceInfo.maxCommission : priceInfo.commission" :bounty="priceInfo.bounty" :more='priceInfo && priceInfo.maxCommission'></commission-label>
                </div>
                <div class="kc" v-if="baseInfo.showStockNum && btnType !==3 && (productInfo.price || productInfo.price === 0) && !sgStockInfo.bookable">库存{{ showStockNum }}件</div>
                <div class="yx">{{ attrSelected }}</div>
              </div>
            </div>
          </div>
          <div class="scroll-view goods-info-info-wrap-content">
            <div class="content-detail">
              <div :key="index" class="content-detail-inner" v-for="(item, index) in attrsMap" :class="index === Object.keys(attrsMap).length-1  ? '__last' : ''">
                <div class="content-detail-inner-tit">{{ item[0].attrName }}</div>
                <div class="content-detail-inner-list">
                  <div
                    :class="{ active: isSelected(v.sgAttribute.id), disable: !isEnable(v.sgAttribute.id, index) }"
                    :key="v.id"
                    @click="handleSelectedAttr(v.sgAttribute.id, index, v)"
                    class="content-detail-inner-item"
                    v-for="v in item"
                  >
                    {{ v.sgAttribute.attrValueName}}
                  </div>
                </div>
              </div>
              <div class="content-detail-inner1">
                <div class="left">数量<span class="limit" v-if="activityLimitFlag">限购{{activityInfo.purchase.purchaseNum}}件,还可购买{{activityInfo.purchase.canBuyNum}}件</span></div>
                <div class="right">
                <div class="customer-stepper">
                  <div :class="{'reduce-icon-gray':goodsNum <= 1,'reduce-icon':goodsNum > 1}" @click="reduce"></div>
                  <input type="number" v-model="goodsNum" class="num-input" :disabled="disabsedInput" :min="minGoodsNum" :max="maxGoodsNum" @change="fnChangeProductNumber" />
                  <div :class="{
                    'plus-icon-gray':plusIconGray,
                    'plus-icon':!plusIconGray}" @click="plus"></div>
                </div>
                </div>
              </div>
              <!-- 白条 -->
              <div class="iouc-container" >
                <div class="header">
                  <div class="title"><span class="title-text">白条</span><span class="title-info">（打白条请到支付页面）</span></div>
                  <div class="tip"><div class="tip-icon"></div><span class="tip-text">6期免息</span></div>
                </div>
                <div class="iouc-content" v-if="ioucInfo && ioucInfo.costInfo">
                  <div class="iouc-item"  v-for="(item, index) in ioucInfo.costInfo" :key="index">
                    <span class="price">￥{{item.eachPrinAndFee}} &times; {{item.num}}期</span>
                    <span class="fee">{{item.fee}}服务费</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
           <div class="btn-container" :class="{'disabled':notSaleStatus || activityDisabledFlag}">
                <template v-if="notSaleStatus">
                  <div class="btn-List-tow">
                    <div class="confirm-btn yellow"><span>加入购物车</span></div>
                    <div class="confirm-btn" :class="{'orange': !(activityInfo && activityInfo.activityType === 70) && getGoodsUseCouponInfo && getGoodsUseCouponInfo.voucherLen && getGoodsUseCouponInfo.type == 'sheng'}">
                      <span>{{checkCouponIsAllGet ? '立即购买' : '领券购买'}}</span>
                      <h6 v-if="getGoodsUseCouponInfo && getGoodsUseCouponInfo.voucherLen && getGoodsUseCouponInfo.type=='sheng' ">到手价{{getGoodsUseCouponInfo.price.toFixed(2)}}</h6>
                    </div>
                  </div>
                </template>
                <template v-else-if="attrLength < attrsMap.length">
                    <div class="confirm-btn" @click="attrUnSelectToast"><span>确定</span></div>
                </template>
                <template v-else>
                  <template v-if="isShowNoStockBtn">
                        <div class="confirm-btn yellow" @click="fnArrivalNotice"><span>到货通知</span></div>
                  </template>
                  <template v-else>
                    <template v-if="btnType===11">
                      <div class="btn-List-tow">
                        <div @click="fullBuy" class="confirm-btn">
                          <div class="origin-price">
                            <span>¥</span>{{ priceInfo.minOriginPrice || priceInfo.originPrice || 0 | formatPrice | formatPriceInt }}<span class="right">.{{ priceInfo.minOriginPrice || priceInfo.originPrice || 0 | formatPrice | formatPriceFloat}}</span>
                          </div>
                          <span class="text">原价购买</span>
                        </div>
                        <div v-if="hasActivityStock" @click="confirm" class="confirm-btn" style="background-color: #FF5252;">
                          <div class="origin-price">
                            <span>¥</span>{{(priceInfo.minPrice || priceInfo.actualPrice || 0) | formatPrice | formatPriceInt}}<span class="right">.{{(priceInfo.minPrice || priceInfo.actualPrice || 0) | formatPrice | formatPriceFloat}}</span>
                          </div>
                          <span class="text">拼团购买</span>
                        </div>
                        <template v-else>
                              <div class="confirm-btn yellow" @click="fnArrivalNotice"><span>到货通知</span></div>
                        </template>
                      </div>
                    </template>
                    <!-- 确认按钮 -->
                    <div class="confirm-btn" v-if="btnType===0 || btnType===1 || btnType === 9 || btnType === 10" @click="confirm"><span>确定</span></div>
                    <div class="confirm-btn yellow" v-if="(btnType===2 || (sgStockInfo && !sgStockInfo.hasStock && attrLength === attrsMap.length && !parentBookable && !sgStockInfo.bookable)) && !notSaleStatus" @click="confirm"><span>到货通知</span></div>
                    <div class="confirm-btn zise" v-if="btnType===8" @click="confirm"><span>立即预约</span></div>
                    <div class="confirm-btn green" v-if="btnType===3" @click="confirm"><span>马上预定</span></div>
                    <div class="confirm-btn full" v-if="btnType===4" @click="confirm"><span>原价购买</span></div>
                    <div class="confirm-btn zise" v-if="btnType===5" @click="confirm"><span><span v-if="activityInfo.deposit !== 1">￥{{ priceInfo.actualPrice ? priceInfo.actualPrice : 0 | formatPrice }}</span>{{activityInfo.deposit === 1 ?'支付订金' : '订金支付'}}</span></div>
                    <div class="btn-List-tow" v-if="btnType===6">
                      <div class="confirm-btn yellow" v-if="baseInfo.supportCart" @click="addCart"><span>加入购物车</span></div>
                      <div class="confirm-btn" :class="{'orange': !(activityInfo && activityInfo.activityType === 70) && getGoodsUseCouponInfo && getGoodsUseCouponInfo.voucherLen && getGoodsUseCouponInfo.type == 'sheng'}" @click="buy">
                        <span>{{ activityInfo && activityInfo.activityType === 70 ? '立即抢购' : checkCouponIsAllGet ? '立即购买' : '领券购买' }}</span>
                        <h6 v-if="getGoodsUseCouponInfo && getGoodsUseCouponInfo.voucherLen && getGoodsUseCouponInfo.type=='sheng'">到手价{{getGoodsUseCouponInfo.price.toFixed(2)}}</h6>
                      </div>
                    </div>
                    <div class="btn-List-tow" v-if="btnType===7">
                      <div class="confirm-btn full" @click="fullBuy"><span>原价购买</span></div>
                      <div class="confirm-btn zise" @click="depositBuy"><span><span v-if="activityInfo.deposit !== 1">￥{{ priceInfo.actualPrice ? priceInfo.actualPrice : 0 | formatPrice }}</span>{{activityInfo.deposit === 1 ?'支付订金' : '订金支付'}}</span></div>
                    </div>
                  </template>
                </template>
              </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ImagePreview } from 'vant'
import { mapGetters } from 'vuex'
const commissionLabel = () => import('@/components/commissionLabel/index')

// import Stepper from './stepper'
export default {
  components: { commissionLabel },
  props: ['sgAttribute', 'sgStoreAttribute', 'productNumber', 'checkCouponIsAllGet', 'getGoodsUseCouponInfo', 'checkedAttr', 'sgStockInfo', 'stepperNum', 'ioucInfo', 'btnType', 'hasSubProduct', 'activityInfo', 'attrLength', 'priceInfo', 'parentBookable', 'notSaleStatus', 'baseInfo', 'products', 'limitSkku'],
  data() {
    return {
      goodsNum: 1,
      productInfo: {}, // 根据规格筛选出的商品信息
      checkedAttrIdsArr: [], // 选择的规格ID
      checkedAttrIdsStr: '', // 选择的规格ID
      attrName: '', // 规格组合名字
      filterAttrs: [], // 可选规格组合筛选后的数据
      originAttrs: [], // 可选规格组合原始数据
      attrsMap: [], // 规格参数，添加selected,enable字段
      priceArr: [], // 价格
      newSgStockInfo: {},
      // priceRange: '', // 多规格时未选中状态展示价格区间
      maxPrice: 0, // 最高价格
      minPrice: 0, // 最高价格
      checkedAttrTypeArr: [], // 已选规格类型
      isShowMemberPrice: this.$util.isShowMemberPrice() // 是否可以显示会员价
    }
  },
  mounted() {
    this.initializationCheck()
    if (this.hasSubProduct) {
      // 多规格默认选中 购买数量 1 ，默认不选中 购买数量 0
      this.goodsNum = this.checkedAttr ? 1 : 0
    }
  },
  computed: {
    ...mapGetters('mymanage', ['displayCommission']),
    attrSelected() {
      return this.attrName.length > 28 ? this.attrName.slice(0, 28) + '...' : this.attrName
    },
    plusIconGray () {
      // 如果在限购活动中goodsNum >= 可购买数量时 加号变灰
      if (this.activityLimitFlag) {
        return this.goodsNum >= this.activityInfo.purchase.canBuyNum
      } else {
        if (this.activityInfo && this.activityInfo.stock > 0) {
          return this.goodsNum >= this.activityInfo.stock
        } else if (this.sgStockInfo.limitNum) {
          if (this.sgStockInfo.bookable || this.parentBookable || (this.products && this.products.isNoLimitStockProduct)) {
            return this.goodsNum >= Math.min(this.sgStockInfo.limitNum, 500)
          }
          return this.goodsNum >= Math.min(this.sgStockInfo.stockNum, this.sgStockInfo.limitNum, 500)
        } else {
          if (this.sgStockInfo.bookable || this.parentBookable || (this.products && this.products.isNoLimitStockProduct)) {
            return this.goodsNum >= 500
          } else {
            if (this.sgStockInfo.bookable || this.parentBookable) {
              return this.goodsNum >= 500
            } else {
              return this.goodsNum >= Math.min(this.sgStockInfo.stockNum, 500)
            }
          }
        }
      }
    },
    /**
     * @description 限购活动标志
     */
    activityLimitFlag() {
      return this.btnType !== 4 && this.btnType !== 9 && this.activityInfo && this.activityInfo.purchase && this.activityInfo.purchase.canBuyNum !== null && this.activityInfo.purchase.purchaseNum !== null
    },
    /**
     * @description 限购活动中可购买数量是否为0
     */
    activityDisabledFlag() {
      return this.activityLimitFlag && this.activityInfo.purchase.canBuyNum === 0
    },
    minGoodsNum() {
      return this.activityDisabledFlag || (this.productInfo && !this.sgStockInfo.bookable && this.productInfo.stockNum === 0) ? 0 : 1
    },
    maxGoodsNum() {
      let num = 0
      if (this.activityLimitFlag) {
        num = this.activityInfo.purchase.canBuyNum
      } else {
        // num = this.products && this.products.isNoLimitStockProduct ? 500 : this.productInfo ? this.productInfo.stockNum > 500 ? 500 : this.productInfo.stockNum : 1
        // Math.min(this.sgStockInfo.stockNum, this.sgStockInfo.limitNum || Infinity, 500)
        if ((this.sgStockInfo && this.sgStockInfo.bookable) || (this.products && this.products.isNoLimitStockProduct)) {
          num = Math.min(this.sgStockInfo.limitNum || Infinity, 500)
        } else {
          num = Math.min(this.sgStockInfo.stockNum, this.sgStockInfo.limitNum || Infinity, 500)
        }
      }

      return num
    },
    disabsedInput() {
      return this.activityDisabledFlag || (this.productInfo && this.productInfo.stockNum === 0 && !this.sgStockInfo.bookable && !(this.products && this.products.isNoLimitStockProduct))
    },
    // 显示库存
    showStockNum() {
      let num = 0;
      if ([10, 11].indexOf(this.btnType) !== -1 && this.activityInfo && (this.activityInfo.stock || this.activityInfo.stock === 0)) {
        num = this.activityInfo.stock
      } else {
        num = this.sgStockInfo ? this.sgStockInfo.stockNum : 0
      }
      return num;
    },
    // 是否有活动库存
    hasActivityStock() {
      return (this.activityInfo && this.activityInfo.stock > 0) || (this.activityInfo.stock === null && this.sgStockInfo && this.sgStockInfo.hasStock);
    },
    // 是否显示到货通知
    isShowNoStockBtn() {
      return (this.activityInfo && this.activityInfo.stock === 0 && [10, 11].indexOf(this.btnType) !== -1 && this.sgStockInfo && !this.sgStockInfo.hasStock) || (this.sgStockInfo && !this.sgStockInfo.hasStock && !this.parentBookable && !this.sgStockInfo.bookable && !this.notSaleStatus);
    }
  },
  filters: {
    formatPriceInt(v) {
      return v.split('.')[0];
    },
    formatPriceFloat(v) {
      return v.split('.')[1];
    }
  },
  watch: {
    activityLimitFlag () {
      this.fnChangeProductNumber()
    },
    productNumber(val) {
      this.goodsNum = val
    },
    /**
     * @description 在限购活动中，如果可购买数量为0，则把goodsNum置为0
     */
    activityDisabledFlag: {
      handler(val) {
        if (val) {
          this.goodsNum = 0
        } else {
          this.goodsNum = 1
        }
      },
      immediate: true
    },
    sgStoreAttribute: {
      handler: function (newValue, oldValue) {
        this.originAttrs = newValue
        if (this.checkedAttr) {
          // 如果已有选中项，用这个
          this.checkedAttrIdsStr = this.checkedAttr
        }
        this.checkedAttrIdsArr = this.checkedAttrIdsStr.split(',').map(v => parseInt(v))
        // 筛选商品信息
        this.combineProductInfo()
      },
      // deep: true,
      immediate: true
    },
    priceInfo: {
      handler: function (newValue, oldValue) {
        if (this.hasSubProduct) {
          // 多规格商品 规格选择完整后 价格 取动态接口配置
          if (this.attrLength === this.attrsMap.length) {
            if (this.productInfo.price !== newValue.actualPrice) {
              this.productInfo.price = newValue.actualPrice
            }
          }
        }
      },
      deep: true,
      // immediate: true
    },
    sgAttribute: {
      handler: function(newValue, oldValue) {
        this.attrsMap = []
        // 将规格对象转换为数组
        for (let key in newValue) {
          let v = newValue[key]
          this.attrsMap.push(v)
        }
        console.log('attrsMap:')
        console.log(this.attrsMap)
      },
      immediate: true
    },
    checkedAttrIdsArr: {
      handler: function(newVal, oldVal) {
        let checkedAttrNamesArr = []
        this.checkedAttrTypeArr = []
        newVal.forEach((v, i) => {
          this.attrsMap[i].forEach(item => {
            if (item.sgAttribute.id === v) {
              checkedAttrNamesArr.push(item.sgAttribute.attrValueName)
              this.checkedAttrTypeArr.push(item.attrName)
            }
          })
        })
        this.attrName = checkedAttrNamesArr.join(',')
        this.$emit('attrNameChange', this.attrName)
      },
    },
    sgStockInfo: {
      deep: true,
      handler(newVal, oldVal) {
        this.productInfo.stockNum = newVal.stockNum;
        // 1)预定商品 2)无需校验库存商品 不做库存限制
        if ((newVal.stockNum > 0 || (this.sgStockInfo.bookable || this.parentBookable) || (this.products && this.products.isNoLimitStockProduct)) && !this.activityDisabledFlag) {
          if (this.goodsNum === 0) {
            this.goodsNum = 1
          }
        } else {
          this.goodsNum = 0
        }
      }
    }
  },
  methods: {
    /**
     * @description 到货通知
     */
    fnArrivalNotice() {
      if (this.notSaleStatus) return
      this.$emit('fnArrivalNotice')
    },
    /**
     * @description  数量减操作
     */
    reduce() {
      if (this.activityDisabledFlag) return
      this.goodsNum = this.goodsNum === 0 ? 0 : this.goodsNum - 1
      this.fnChangeProductNumber()
    },
    /**
     * @description  数量加操作
     */
    plus() {
      if (this.plusIconGray) return
      this.goodsNum = this.goodsNum >= 500 ? 500 : this.goodsNum + 1
      this.fnChangeProductNumber()
    },
    /**
     * @description  初始化选中名称
     */
    initializationCheck() {
      // if (!this.hasSubProduct) {
      let checkedAttrNamesArrs = []
      this.checkedAttrIdsArr.forEach((v, i) => {
        this.attrsMap[i].forEach(item => {
          if (item.sgAttribute.id === v) checkedAttrNamesArrs.push(item.sgAttribute.attrValueName)
        })
      })
      this.attrName = checkedAttrNamesArrs.join(',')
      this.$emit('attrNameChange', this.attrName)
      // }
    },
    /**
     * @description  判断某规格是否可选
     * @param {string} id 规格id
     * @param {number} parentIndex 规格分类下标
     */
    isEnable(id, parentIndex) {
      let tmpCheckedAttrIdsArr = [...this.checkedAttrIdsArr]
      let tmpFilterAttrs = [...this.originAttrs]
      tmpCheckedAttrIdsArr.forEach((checkedAttr, index) => {
        if (index !== parentIndex && checkedAttr) {
          tmpFilterAttrs = tmpFilterAttrs.filter((v, i) => v.attrValueIds.indexOf(checkedAttr) !== -1)
        }
      })
      for (const filter of tmpFilterAttrs) {
        if (this.sgStockInfo.bookable) {
          // 马上预定的商品 只禁用上级规格不对应的下级规格
          if (filter.attrValueIds.indexOf(id) !== -1) {
            return true
          }
        } else {
          if (!this.hasSubProduct) {
            return true
          } else {
            // 非马上预定 增加库存数限制 ，静态接口无库存 禁用 sgStockInfo && !sgStockInfo.stockNum
            if (filter.attrValueIds.indexOf(id) !== -1 && (filter.num && filter.num > 0)) {
              if (this.limitSkku) {
                if (this.limitSkku.indexOf(filter.skku) !== -1) {
                  return true
                }
              } else {
                return true
              }
            }
          }
        }
      }
      return false
    },
    /**
     * @description  判断某规格是否选中
     * @param {string} id 规格id
     */
    isSelected(id) {
      return this.checkedAttrIdsArr.indexOf(id) !== -1
    },
    /**
     * @description  选择某个规格
     * @param {string} id 所选规格id
     * @param {number} parentIndex 规格分类下标
     * @param {object} item 选中的规格
     */
    handleSelectedAttr(id, parentIndex, item) {
      if (!this.hasSubProduct) return // 单规格商品不可切换
      if (!this.isEnable(id, parentIndex)) return
      // 替换选择的规格ID, 如果是取消选中，id赋值null
      let tmpAttrId = this.isSelected(id) ? null : id
      this.$set(this.checkedAttrIdsArr, parentIndex, tmpAttrId)
      this.combineProductInfo()
      this.goodsNum = 1
      this.$emit('changeGoodsNumber', this.goodsNum)
      this.$emit('fnSelectAttribute', this.productInfo)
      this.$emit('changeAttribute', item)
    },
    /**
     * @description 规则未选完整提示
    */
    attrUnSelectToast() {
      if (this.notSaleStatus || this.activityDisabledFlag) return
      if (this.checkedAttrTypeArr.length < this.attrsMap.length) {
        let unSelectList = []
        this.attrsMap.forEach(i => {
          if (this.checkedAttrTypeArr.indexOf(i[0].attrName) === -1) {
            unSelectList.push(i[0].attrName)
          }
        })
        this.$toast(`请选择 ${unSelectList.join(',')}`)
      }
    },
    // 筛选商品信息
    combineProductInfo() {
      this.checkedAttrIdsStr = this.checkedAttrIdsArr.join(',')
      this.productInfo = {}
      let attrIds = (this.checkedAttrIdsStr || '').split(',').sort().join(',')
      for (const v of this.originAttrs) {
        let valueIds = (v.attrValueIds || '').split(',').sort().join(',')
        if (attrIds === valueIds) {
        // if (this.checkedAttrIdsStr === v.attrValueIds) {
          // this.priceRange = ''
          this.productInfo = v
          if ((this.productInfo.price || this.productInfo.price === 0) && this.productInfo.price !== 'NaN') {
            this.priceArr = (parseFloat(this.productInfo.price).toFixed(2) + '').split('.')
          } else {
            this.priceArr = ['0']
          }
          break
        }
      }
      if (!this.productInfo.price) {
        // 无选中状态 针对多规格商品处理 1) 展示图片取默认第一张 2)计算所有规格价格区间
        this.$emit('priceValue', '')

        if (this.hasSubProduct) {
          this.productInfo = {
            pic: this.originAttrs[0].pic
          }
          // // 计算拼接 价格区间
          // let max = 0
          // let min = 100000000
          // this.originAttrs.forEach(ii => {
          //   if (ii.price > max) max = ii.price
          //   if (ii.price < min) min = ii.price
          // })
          // this.minPrice = min
          // this.maxPrice = max

          // this.priceRange = `￥${min}-${max}`
          // this.$emit('priceRange', min)
        }
      } else {
        // this.$emit('priceRange', '')
        this.$emit('priceValue', this.productInfo.price)
      }
    },
    fnShowInfo() {
      this.$emit('fnShowInfo')
    },
    fnPreviewImage(pic) {
      let images = [pic]
      ImagePreview({
        images,
        startPosition: 0,
        onClose() {
          // do something
        },
      })
    },
    fnChangeProductNumber(value) {
      this.checkNumber()
      this.$emit('fnChangeProductNumber', this.goodsNum)
    },
    /**
     * @description  检查输入数量是否合法
     */
    checkNumber() {
      if (this.goodsNum < 1 && !this.activityDisabledFlag) {
        this.goodsNum = 1
        return
      }
      // DSG-25621
      if (this.activityLimitFlag) {
        this.goodsNum = this.goodsNum > this.maxGoodsNum ? this.maxGoodsNum : this.goodsNum
      } else {
        if (this.activityInfo && this.activityInfo.stock > 0) {
          if (this.goodsNum > this.activityInfo.stock) {
            this.goodsNum = this.activityInfo.stock
          }
        } else if (this.sgStockInfo && this.sgStockInfo.limitNum) {
          if (this.sgStockInfo.bookable || this.parentBookable || (this.products && this.products.isNoLimitStockProduct)) {
            const limitNum = Math.min(this.sgStockInfo.limitNum || Infinity, 500)
            if (this.goodsNum > limitNum) {
              this.goodsNum = limitNum
              this.$toast(`为了您的购物体验，当前商品一次购买数量不超过${limitNum}`)
            }
          } else {
            const limitNum = Math.min(this.sgStockInfo.stockNum, this.sgStockInfo.limitNum || Infinity, 500)
            if (this.goodsNum > limitNum) {
              this.goodsNum = limitNum
              this.$toast(`为了您的购物体验，当前商品一次购买数量不超过${limitNum}`)
            }
          }
        } else {
          if (this.sgStockInfo.bookable || this.parentBookable || (this.products && this.products.isNoLimitStockProduct)) {
            const limitNum = 500
            if (this.goodsNum > limitNum) {
              this.goodsNum = limitNum
            }
          } else {
            const limitNum = Math.min(this.sgStockInfo.stockNum, 500)
            if (this.goodsNum > limitNum) {
              this.goodsNum = limitNum
            }
          }
        }

        // if (!this.sgStockInfo.limitNum && this.products.isNoLimitStockProduct) {
        //   if (this.goodsNum > 500) this.goodsNum = 500
        // } else {
        //   const limitNum = Math.min(this.sgStockInfo.stockNum, this.sgStockInfo.limitNum || Infinity, 500)
        //   if (this.goodsNum > limitNum) {
        //     this.goodsNum = limitNum
        //     if (this.sgStockInfo.limitNum && this.sgStockInfo.limitNum <= this.sgStockInfo.stockNum) {
        //       this.$toast(`为了您的购物体验，当前商品一次购买数量不超过${this.sgStockInfo.limitNum}`)
        //     }
        //   }
        // }
      }
    },
    fnAddCart() {
      if (this.notSaleStatus || this.activityDisabledFlag) return
      this.$emit('fnAddCart')
    },
    fnGoBuy() {
      if (this.notSaleStatus || this.activityDisabledFlag) return
      this.$emit('fnGoBuy')
    },
    /**
     * @description  确定
     */
    confirm() {
      if (this.notSaleStatus || this.activityDisabledFlag) return
      if ((this.goodsNum > 0 && this.attrSelected) || this.btnType === 2) {
        this.$emit('attrConfirm', this.goodsNum)
      }
    },
    /**
     * @description  加购物车
     */
    addCart() {
      if (this.notSaleStatus || this.activityDisabledFlag) return
      if (this.goodsNum > 0 && this.attrSelected) {
        this.$emit('addCart')
      }
    },
    /**
     * @description  立即购买
     */
    buy() {
      if (this.notSaleStatus || this.activityDisabledFlag) return
      if (this.goodsNum > 0 && this.attrSelected) {
        this.$emit('buy')
      }
    },
    depositBuy() {
      if (this.notSaleStatus || this.activityDisabledFlag) return
      if (this.goodsNum > 0 && this.attrSelected) {
        this.$emit('depositBuy')
      }
    },
    fullBuy() {
      if (this.notSaleStatus || this.activityDisabledFlag) return
      if (this.goodsNum > 0 && this.attrSelected) {
        this.$emit('fullBuy')
      }
    },
  },
}
</script>
